import React from "react";
import "../../scss/StartupProjects.scss";
import {Fade} from "react-reveal";
import SoftwareSkill from "./SoftwareSkill";
import Carousel from "react-bootstrap/Carousel";

export default function StartupProject({bigProjects}) {
  function openUrlInNewTab(url) {
    if (!url) {
      return;
    }
    var win = window.open(url, "_blank");
    win.focus();
  }
  const ProjectGetDescBullets = ({descBullets}) => {
    return descBullets
      ? descBullets.map((item, i) => (
          <li key={i}>
            {item}
          </li>
        ))
      : null;
  };

  function GetProjectVis({project}) {
    if (project.projectVisType === "image")
      return (
        <img
          src={project.image}
          alt={project.projectName}
          className="card-image"
        ></img>
      );
    else if (project.projectVisType === "video")
      return (
        <video
          src={project.image}
          alt={project.projectName}
          className="card-image"
          controls
        ></video>
      );
    else if (project.projectVisType === "caro")
      return (
        <Carousel>
          {project.image.map((value, index) => {
            if (value.visType === "image")
              return (
                <Carousel.Item>
                  <img
                    className="card-caro"
                    src={value.img}
                    alt="First slide"
                  />
                  <Carousel.Caption>
                    <h5 style={{color: '#999999'}}>{value.label}</h5>
                    <p style={{color: '#999999'}}>
                      {value.paragraph}
                    </p>
                  </Carousel.Caption>
                </Carousel.Item>
              );
            else if (value.visType === "video")
              return (
                <Carousel.Item>
                  <video
                    className="card-caro"
                    src={value.img}
                    alt="First slide"
                    controls
                  />
                  <Carousel.Caption>
                    <h5>{value.label}</h5>
                    <p>
                      {value.paragraph}
                    </p>
                  </Carousel.Caption>
                </Carousel.Item>
              );


          })}
        </Carousel>
      );

  }

  /*
  if (!bigProjects.show) {
    return null;
  }
  */
  return (
    <section>
      <div className="main" id="projects">
        <div>
          <h2 className="display-4 pb-5 text-center">{bigProjects.title}</h2>
          <p className="subTitle project-subtitle">
            {bigProjects.subtitle}
          </p>

          <div className="projects-container">
            {bigProjects.projects.map((project, i) => {
              return (
                <div
                  key={i}
                  className="project-card project-card-light"
                >
                  {project.image ? (
                    <div className="project-image">
                      <GetProjectVis project={project} />
                    </div>
                  ) : null}
                  <div className="project-detail">
                    <h5 className="card-title">
                      {project.projectName}
                    </h5>
                    <p className="card-text-date">
                      {project.projectDate}
                    </p>
                    {project.projectNote ? (
                      <p style={{color: "red"}}><b>{project.projectNote}</b></p>
                    ) : null}
                    {project.projectSkills ? (<SoftwareSkill softwareSkills={project.projectSkills} />) : null}
                    <p className="card-subtitle text-left">
                      <ul>
                        <ProjectGetDescBullets descBullets={project.projectDesc} />
                      </ul>
                     
                    </p>
                    {project.footerLink ? (
                      <div className="project-card-footer">
                        {project.footerLink.map((link, i) => {
                          return (
                            <span
                              key={i}
                              className="project-tag"
                              onClick={() => openUrlInNewTab(link.url)}
                            >
                              {link.name}
                            </span>
                          );
                        })}
                      </div>
                    ) : null}
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </section>
  );
}