import React from "react";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import SkillsTab from "./SkillsTab";
import Row from "react-bootstrap/Row";
import { Jumbotron, Container } from "react-bootstrap";
import { useScrollPosition } from "../../hooks/useScrollPosition";

function Skills({ heading, languageSkills, librariesFrameSkills}) {
  const skillsTabRef = React.useRef();
  const [isScrolled, setIsScrolled] = React.useState(false);
  //const navbarDimensions = useResizeObserver(navbarMenuRef);

  useScrollPosition(
    ({ prevPos, currPos }) => {
      if (!isScrolled && currPos.y - 400 < 0) setIsScrolled(true);
    },
    [],
    skillsTabRef
  );
  return (
    <Jumbotron ref={skillsTabRef} fluid className="bg-gray-200 m-0" id="skills">
      <Container fluid className="p-5">
        <h2 ref={skillsTabRef} className="display-4 pb-5 text-center">
          {heading}
        </h2>
        <Tabs fluid
          className="skills-tabs"
          defaultActiveKey="all-skills"
          id="skills-tabs"
        >
          <Tab
            tabClassName="skills-tab lead"
            eventKey="all-skills"
            title="All Skills"
          >
            <Row className="pt-3 px-1">
              <SkillsTab skills={languageSkills.concat(librariesFrameSkills)} isScrolled={isScrolled} />
            </Row>
          </Tab>
          <Tab
            tabClassName="skills-tab lead"
            eventKey="langauges-databases-skills"
            title="Languages/Databases"
          >
            <Row className="pt-3 px-1">
              <SkillsTab skills={languageSkills} isScrolled={isScrolled} />
            </Row>
          </Tab>
          <Tab
            tabClassName="skills-tab lead"
            eventKey="libraries-frameworks-skills"
            title="Libraries/Frameworks/Tools/Other"
          >
            <Row className="pt-3 px-1">
              <SkillsTab skills={librariesFrameSkills} isScrolled={isScrolled} />
            </Row>
          </Tab>

        </Tabs>
      </Container>
    </Jumbotron>
  );
}

export default Skills;
