import React from "react";
import Typist from "react-typist";
import Container from "react-bootstrap/Container";
import Jumbotron from "react-bootstrap/Jumbotron";

const MainBody = React.forwardRef(
  ({ gradient, personalLogo, title, message, icons }, ref) => {
    return (
      <Jumbotron
        fluid
        id="home"
        style={{
          background: `linear-gradient(136deg,${gradient})`,
          backgroundSize: "1200% 1200%",
        }}
        className="title bg-transparent bgstyle text-light min-vh-100 d-flex align-content-center align-items-center flex-wrap m-0"
      >
        <div id="stars"></div>
        <Container className="text-center">

          <img src={personalLogo} style={{borderRadius: "500%"}} width="180px"></img>
      
          <h1 ref={ref} className="display-1">
            {title}
          </h1>
          <Typist className="lead typist" cursor={{ show: false }}>
            {" "}
            {message}
          </Typist>
          {/*
          <div className="p-5">
            {icons.map((icon, index) => (
              <a
                key={`social-icon-${index}`}
                target="_blank"
                rel="noopener noreferrer"
                href={icon.url}
                aria-label={`My ${icon.image.split("-")[1]}`}
              >
                <i className={`fab ${icon.image}  fa-3x socialicons`} />
              </a>
            ))}
          </div>
          */}
          <div className="text-center">
            <a
              className="btn btn-outline-light btn-lg mr-2"
              href="#aboutme"
              role="button"
              aria-label="Learn more about me"
            >
              About me
            </a>
            <a>
              {icons.map((icon, index) => (
                  <a
                    key={`social-icon-${index}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    href={icon.url}
                    aria-label={`My ${icon.image.split("-")[1]}`}
                  >
                    <i className={`fab ${icon.image}  fa-3x socialicons`} />
                  </a>
                ))}
            </a>
            <a
              className="btn btn-outline-light btn-lg ml-2"
              href='mailto:claire.siroky@utexas.edu'
              role="button"
              aria-label="email me"
            >
              Email
            </a>
          </div>
        </Container>
      </Jumbotron>
    );
  }
);

export default MainBody;
